import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';


const DirectoryView = (props) => (
    <div className='directView'>
        <SearchForm SearchVal={props.SearchInput} handler={props.handler} selected={props.selected} />
        {props.recipes.map((recipe, i) => <RecipeCard name={recipe.name} number={recipe.id} update={props.update} key={i} />)}
    </div>
);

const SearchForm = (props) => (
    <form>
        <input className='input-box' value={props.searchVal} placeholder='Search recipe here...' onChange={props.handler} />
        <button onClick={props.selected}>Search</button>
    </form>
)
const RecipeCard = (props) => (
    <div>
        <p className='card' id={props.number} onClick={props.update}>{props.name}</p>
    </div>
);

const DetailView = (props) => (
    <div className='inner-right-container'>
        {props.eachRecipe.map((elem, i) => <HeadingCard name={elem.name} img={elem.photo} key={i} />)}
        <div className="separate">
            <div className='left'>
                <h3>Ingredients</h3>
                {props.eachIngredient.map((elem, i) => elem.ingredients.map((e, i) => <IngredientCard name={e} key={i} />))}
            </div>
            <div className='right'>
                <h3>Instructions</h3>
                {props.eachInstruction.map((elem, i) => elem.instructions.map((e, i) => <InstructionCard name={e} key={i} />))}
            </div>
        </div>
    </div>
);

const HeadingCard = (props) => (
    <div className='heading-card'>
        <img alt='' className='food-img' src={props.img} style={{ display: 'inline-block' }}></img>
        <h2 className='food-head' style={{ display: 'inline-block' }}>{props.name}</h2>
    </div>
);
const IngredientCard = (props) => (
    <p className='each-left'>{props.name}</p>
);

const InstructionCard = (props) => (
    <p className='each-right'>{props.name}</p>
);
