    handleChange = (event) => {
        console.log({ SearchInput: event.target.value })
        this.setState({ SearchInput: event.target.value });
    }

    selectRecipes = (event) => {
        event.preventDefault();
        const newArray = this.state.foods.filter(recipe => recipe.name.toLowerCase().indexOf(this.state.SearchInput.toLowerCase()) > -1)
        this.setState({selectedRecipes: newArray})
    }
    detailedClick = (event) => {
        event.preventDefault();
        let num = event.target.id
        const newArray = this.state.foods.filter(recipe => recipe.id === Number(num))
        this.setState({ selectedIngredients: newArray })

    }

